// @flow
import { ButtonActions } from '../constants/ButtonConstants';
import { handleActions } from 'redux-actions';
import updeep from 'updeep';

const _success = {
	next(state, action) {
		return updeep(
			{
				isFetching: false,
				error     : false,
				loaded    : true,
			}, state);
	},
};
const _error = {
	next(state) {
		return updeep(
			{
				isFetching: false,
				error     : true,
			}, state);
	},
};

const _request = {
	next(state, action) {
		return updeep(
			{
				isFetching : true,
				error      : false,
			}, state);
	},
};

const buttonReducer = {
	[buttonActions.BUTTON_REQUEST]          : _request,
	[buttonActions.BUTTON_SUCCESS]          : _success,
	[buttonActions.BUTTON_ERROR]            : _error,
};

export default handleActions(buttonReducer, {
	isFetching : false,
	error      : false,
});

